.contact-wrapper {
    position: sticky;
    top: 12px;
    height: calc(100vh - 24px);
}

.contact-section {
    padding: 24px;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.contact-description {
    margin-bottom: 24px;
    color: var(--text-secondary);
    line-height: 1.6;
}

.social-links {
    display: flex;
    flex-direction: column;
    gap: 16px;
    margin-bottom: 32px;
}

.social-link {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 12px 24px;
    background: linear-gradient(135deg, var(--primary), var(--accent));
    border-radius: 8px;
    color: white;
    text-decoration: none;
    font-size: 1rem;
    font-weight: 500;
    transition: all 0.3s ease;
}

.social-link:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(93, 84, 199, 0.4);
}

.social-link svg {
    font-size: 1.2rem;
}

.whatsapp-link {
    background: linear-gradient(135deg, #25D366, #128C7E);
}

.whatsapp-link:hover {
    box-shadow: 0 6px 20px rgba(37, 211, 102, 0.4);
}

@media (max-width: 1400px) {
    .contact-wrapper {
        position: relative;
        top: 0;
        height: auto;
    }

    .contact-section {
        height: auto;
    }
}

@media (max-width: 768px) {
    .social-links {
        flex-direction: column;
        gap: 16px;
    }

    .social-link {
        justify-content: center;
    }

    .contact-section {
        padding: 20px;
    }
}

@media (max-width: 480px) {
    .contact-section {
        padding: 12px;
    }

    .social-link {
        padding: 10px 20px;
        font-size: 0.9rem;
    }
}